*::-webkit-scrollbar {
	width: 6px
}

*::-webkit-scrollbar-track {
	background: var(--c-background)
}

*::-webkit-scrollbar-thumb {
	background-color: var(--c-background-2);
	border-radius: 20px;
	border: 3px solid var(--c-background-2)
}

.tippy-box[data-theme~=swampy] {
	background-color: var(--c-background-2);
	color: var(--c-txt)
}

.tippy-box[data-theme~=swampy][data-placement^=top]>.tippy-arrow::before {
	border-top-color: var(--c-background-2)
}

.tippy-box[data-theme~=swampy][data-placement^=bottom]>.tippy-arrow::before {
	border-bottom-color: var(--c-background-2)
}

.tippy-box[data-theme~=swampy][data-placement^=left]>.tippy-arrow::before {
	border-left-color: var(--c-background-2)
}

.tippy-box[data-theme~=swampy][data-placement^=right]>.tippy-arrow::before {
	border-right-color: var(--c-background-2)
}

.mt-10 {
	margin-top: 10px
}

.mt-20 {
	margin-top: 20px
}

.ml-auto {
	margin-left: auto
}

.ml-6 {
	margin-left: 6px
}

.ml-10 {
	margin-left: 10px
}

.ml-20 {
	margin-left: 20px
}

.txt {
	font-size: 16px
}

.btn {
	width: fit-content;
	height: 50px;
	background-color: var(--c-secondary);
	color: var(--c-txt);
	line-height: 50px;
	border-radius: var(--r-border);
	cursor: pointer;
	transition: all .2s ease;
	font-size: 16px;
	font-weight: 500;
	text-align: center
}

.btn.loading {
	position: relative;
	font-size: 0;
	pointer-events: none
}

.btn.loading::after {
	content: '';
	position: absolute;
	display: block;
	width: 20px;
	height: 20px;
	border: 3px solid var(--c-background-3);
	border-top: 3px solid var(--c-primary);
	border-radius: 50%;
	animation: rotate 1s linear infinite;
	left: calc(50% - 10px);
	top: 11px
}

.btn:hover {
	background-color: var(--c-secondary-hover)
}

.btn.primary {
	background-color: var(--c-primary);
	box-shadow: var(--t-shadow-1);
	color: var(--c-txt-3)
}

.btn.primary:hover {
	background-color: var(--c-primary-hover)
}

.btn.secondary {
	background-color: var(--c-background-2);
	color: var(--c-txt);
	box-shadow: none
}

.btn.secondary:hover {
	background-color: var(--c-secondary-hover)
}

.btn.small {
	height: 40px;
	line-height: 40px;
	padding: 0 15px
}

.btn-close {
	position: relative;
	display: block;
	width: 12px;
	height: 12px;
	background-image: url('https://chilliswap.finance/static/frontend/img/close.svg')
}

.btn-close::before {
	position: absolute;
	display: block;
	content: "";
	width: 24px;
	height: 24px;
	border-radius: 50%;
	opacity: 0;
	transition: opacity .2s ease;
	background-color: var(--c-background-2);
	left: -6px;
	top: -6px;
	cursor: pointer
}

.btn-close:hover::before {
	opacity: .3
}

.btn.outlined {
	width: 180px;
	height: 50px;
	line-height: 48px;
	text-align: center;
	border: var(--c-txt-2) 2px solid;
	color: var(--c-txt-2);
	background-color: transparent
}

.btn.outlined:hover {
	border-color: var(--c-primary);
	color: var(--c-txt-3);
	background-color: var(--c-primary)
}

.btn.expand {
	position: relative;
	width: 50px;
	height: 50px;
	background-color: var(--c-background-4)
}

.btn.expand:hover {
	background-color: var(--c-background-2)
}

.btn.expand::after {
	content: '';
	position: absolute;
	width: 17px;
	height: 11px;
	background: url('../src/img/arrow-down.svg') no-repeat;
	left: calc(50% - 8px);
	top: calc(50% - 5px);
	transition: transform .2s ease
}

.btn.expand.expanded {
	background-color: var(--c-background-2)
}

.btn.expand.expanded::after {
	transform: rotateZ(180deg)
}

.dropdown-wrapper {
	position: relative;
	min-width: 175px;
	height: 50px;
	padding: 0 23px;
	color: var(--c-txt);
	font-size: 16px;
	background: 0 0;
	appearance: none;
	border: var(--c-txt-2) solid 2px;
	border-radius: var(--r-border);
	outline: none;
	transition: border .2s ease;
	cursor: pointer
}

.dropdown-wrapper::before {
	content: attr(data-name);
	position: absolute;
	top: 10px;
	font-size: 15px
}

.dropdown-wrapper::after {
	content: '';
	position: absolute;
	display: block;
	width: 17px;
	height: 11px;
	right: 23px;
	top: calc(50% - 5px);
	background: url('../src/img/arrow-down.svg') no-repeat;
	background-size: 15px 10px;
	transition: transform .2s ease
}

.dropdown-wrapper:hover {
	border-color: var(--c-txt)
}

.dropdown-wrapper.opened {
	border-color: var(--c-primary)
}

.dropdown-wrapper.opened::after {
	transform: rotateZ(180deg)
}

.dropdown-wrapper.opened .dropdown {
	display: block;
	animation: dropdown-fly-in .2s ease;
	pointer-events: auto;
	z-index: 10;
	user-select: none
}

.dropdown-wrapper.wrapped .dropdown {
	max-height: 300px;
	overflow-y: auto;
	border-radius: var(--r-border) 0 0 var(--r-border)
}

.dropdown-wrapper.wrapped .dropdown .itm:first-of-type {
	border-radius: var(--r-border) 0 0 0
}

.dropdown-wrapper.wrapped .dropdown .itm:last-of-type {
	border-radius: 0 0 0 var(--r-border)
}

.dropdown-wrapper .dropdown {
	position: absolute;
	display: none;
	width: 100%;
	top: calc(100% + 14px);
	left: 0;
	border-radius: var(--r-border);
	background-color: var(--c-background-4);
	box-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
	pointer-events: none
}

.dropdown-wrapper .dropdown .itm {
	font-size: 15px;
	padding: 15px 24px;
	transition: background-color .2s ease
}

.dropdown-wrapper .dropdown .itm:hover {
	background-color: var(--c-txt-2)
}

.dropdown-wrapper .dropdown .itm:first-of-type {
	border-radius: var(--r-border) var(--r-border) 0 0
}

.dropdown-wrapper .dropdown .itm:last-of-type {
	border-radius: 0 0 var(--r-border) var(--r-border)
}

.dropdown-wrapper.overflown .overflown {
	max-height: 400px;
	overflow-y: auto
}

input[type=text] {
	position: relative;
	width: 244px;
	/* height: 50px; */
	padding: 0 40px 0 23px;
	background: 0 0;
	/* border: var(--c-txt-2) solid 2px; */
	border-radius: var(--r-border);
	font-size: 16px;
	color: var(--c-txt);
	outline: none;
	transition: border-color .2s ease
}

input[type=text]:focus {
	/* border-color: var(--c-txt) */
}

input[type=text]::placeholder {
	/* color: var(--c-txt-2) */
}

.input-container {
	position: relative;
	width: fit-content
}

.input-container.search::after {
	content: '';
	position: absolute;
	display: block;
	width: 16px;
	height: 16px;
	background: url('../src/img/search.svg');
	top: calc(50% - 4px);
	right: 23px
}

.input-container.with-max .max {
	position: absolute;
	display: block;
	padding: 5px 8px;
	background-color: var(--c-background-3);
	border-radius: 6px;
	color: var(--c-primary);
	font-size: 13px;
	cursor: pointer;
	right: 15px;
	top: 10px;
	transition: background-color .2s ease
}

.input-container.with-max .max:hover {
	background-color: var(--c-background-2)
}

input[type=checkbox] {
	position: relative;
	width: 30px;
	height: 30px;
	border: var(--c-txt-2) solid 2px;
	border-radius: 5px;
	appearance: none;
	outline: none;
	transition: all .2s ease;
	cursor: pointer
}

input[type=checkbox]:checked {
	background-color: var(--c-primary);
	box-shadow: var(--t-shadow-2);
	border-color: var(--c-primary)
}

input[type=checkbox]:checked::before {
	position: absolute;
	content: '';
	width: 7px;
	height: 11px;
	border: 2px solid var(--c-background);
	border-top: 0;
	border-left: 0;
	left: 9px;
	top: 4px;
	transform: rotate(40deg);
	background-color: var(--c-primary)
}

.checkbox-container {
	display: flex;
	flex-direction: row;
	align-items: center
}

.checkbox-container>label {
	margin: 0 10px;
	color: var(--c-txt-2);
	cursor: pointer
}

.tag {
	height: 30px;
	line-height: 30px;
	font-size: 15px;
	background-color: var(--c-primary);
	color: var(--c-txt-3);
	font-weight: 700;
	border-radius: var(--r-border-2)
}

.mini-tag {
	position: relative;
	display: block;
	font-size: 11px;
	background-color: var(--c-background-2);
	border-radius: var(--r-border-2);
	border: var(--c-primary) 2px solid;
	padding: 2px 4px;
	color: var(--c-primary);
	box-shadow: var(--t-shadow-3)
}

.download-card {
	display: flex;
	width: 100%;
	height: 100px;
	background-color: var(--c-background-3);
	border-radius: var(--r-border);
	align-items: center;
	padding: 0 30px
}

.download-card .icon {
	width: 60px;
	height: 60px
}

.download-card .txt {
	margin-left: 20px
}

.download-card .txt .ttl {
	font-size: 18px;
	color: var(--c-txt)
}

.download-card .txt .desc {
	font-size: 15px;
	line-height: 18px;
	color: var(--c-txt-2)
}

.download-card .btn {
	display: block;
	margin-left: auto;
	padding: 0 16px
}

body {
	/* background-color: var(--c-background); */
	color: var(--c-txt)
}

header {
  background-color:#0000004d;
	position: absolute;
	display: flex;
	width: 100%;
	flex-direction: column;
	top: 0;
	left: 0;
	z-index: 10;
	transition: background-color .2s ease
}

header.sticky {
	position: fixed;
	top: 0;
	left: 0;
	background-color: var(--c-background);
	animation: header-sticky .5s ease
}

header.mobile {
	position: fixed;
	top: 0;
	left: 0
}

header.sticky.mobile {
	animation: none
}

header .top-notification {
	position: relative;
	display: flex;
	width: 100%;
	height: 30px;
	background-color: var(--c-primary);
	color: var(--c-background);
	font-size: 14px;
	justify-content: center;
	align-items: center;
	line-height: 30px;
	z-index: 11
}

header .top-notification.hidden {
	display: none
}

header .top-notification.visible {
	animation: top-notification-in .4s ease-in-out forwards
}

header .top-notification .btn-close {
	position: absolute;
	right: 20px
}

header .container {
	position: relative;
	display: flex;
	height: 76px;
	flex-direction: row;
	padding: 18px 44px;
	align-items: center;
	z-index: 9
}

header .logo {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: center
}

header .logo>a {
	display: flex;
	flex-direction: row
}

header .logo a img {
	width: 35px;
	width: 50px
}

header .logo .txt {
	position: relative;
	color: #DA251E;
	text-shadow: var(--t-shadow-2);
	font-size: 20px;
	top: 2px;
	left: 10px
}

header .logo .txt::after {
	content: 'BETA';
	position: absolute;
	color: var(--c-primary);
	font-size: 11px;
	left: calc(100% + 4px);
	font-weight: 700
}

header menu {
	position: absolute;
	width: fit-content;
	// left: calc(50% - var(--content-width)/2)
}

header menu ul {
	display: flex;
	flex-direction: row;
	color: var(--c-txt-2);
	font-size: 16px;
	pointer-events: none
}

header menu ul li a,
header menu ul li a:visited {
	color: inherit
}

header menu ul li {
	pointer-events: auto;
	transition: color .2s ease
}

header menu ul li a {
	color: inherit
}

header menu ul li.selected {
	color: var(--c-primary);
	text-shadow: var(--t-shadow-1)
}

header menu ul li:not(:last-of-type) {
	margin-right: 60px
}

header menu ul li:hover:not(.selected) {
	color: var(--c-txt)
}

header .wallet {
	display: flex;
	flex-direction: row;
	margin-left: auto
}

header .wallet .swamp-price {
	display: flex;
	flex-direction: row;
	align-items: center
}

header .wallet .swamp-price img {
	width: 35px;
	height: 35px
}

header .wallet .swamp-price .price {
	text-shadow: var(--t-shadow-1);
	color: var(--c-primary)
}

header .wallet .swamp-balance.hidden {
	display: none
}

header .wallet .btn-wallet {
	position: relative;
	padding: 0 30px 0 70px
}

header .wallet .btn-wallet::before {
	position: absolute;
	content: '';
	width: 20px;
	height: 20px;
	background-image: url('../src/img/wallet.svg');
	left: 30px;
	top: 9px
}

header .wallet .balance {
	display: flex;
	position: relative;
	width: fit-content;
	background-color: var(--c-txt-2);
	color: var(--c-txt);
	border-radius: var(--r-border);
	padding: 0 0 0 20px;
	flex-direction: row;
	align-items: center;
	cursor: pointer
}

header .wallet .balance.hidden {
	display: none
}

header .wallet .balance .wallet-info {
	display: flex;
	height: 100%;
	padding: 0 20px;
	background-color: var(--c-background-2);
	color: var(--c-primary);
	border-radius: var(--r-border);
	align-items: center;
	margin-left: 20px
}

header .wallet .balance .wallet-info .icon {
	width: 30px;
	height: 30px;
	border-radius: 50%
