header .wallet .balance .wallet-info .icon canvas {
	width: 100%;
	height: 100%;
	border-radius: 50%
}

header .hamburger {
	position: absolute;
	display: none;
	width: 24px;
	height: 24px;
	right: 45px;
	top: 27px
}

header .hamburger svg path {
	fill: var(--c-primary)
}

header .mobile-menu {
	position: fixed;
	display: none;
	width: 100vw;
	height: 100vh;
	top: 100%;
	left: 0;
	background-color: var(--c-background);
	padding: 150px 0;
	z-index: 8;
	align-items: center;
	justify-content: center;
	animation: menu-visible .5s ease
}

header .mobile-menu.visible {
	display: block;
	top: 0
}

header .mobile-menu .menu {
	display: flex;
	justify-content: center
}

header .mobile-menu .menu a {
	color: var(--c-txt-2)
}

header .mobile-menu .menu ul {
	width: 400px;
	text-align: center
}

header .mobile-menu .menu ul li {
	color: var(--c-txt-2);
	font-size: 18px;
	margin-bottom: 20px
}

header .mobile-menu .menu ul li.selected a {
	color: var(--c-primary);
	text-shadow: var(--t-shadow-2)
}

header .mobile-menu .wallet {
	display: none;
	padding-bottom: 40px;
	margin: 0;
	justify-content: center;
	flex-wrap: wrap
}

header .mobile-menu .wallet .break {
	flex-basis: 100%
}

header .mobile-menu .wallet .balance {
	height: 40px
}

header .mobile-menu .wallet .swamp-price,
header .mobile-menu .wallet .buy-swamp {
	margin-top: 30px
